@import "src/assets/variables";
@import "src/assets/@mixin";

.consultation_section {
  margin-top: 104px;
  background: var(--hover, linear-gradient(180deg, #8D1038 0%, #191923 100%));

}

.consultation_body {
  padding: 70px 0 30px;
  border-radius: 4px;
  display: flex;
}

.consultation_text {
  flex: 1 1 298px;
  display: flex;
  flex-direction: column;

  h3 {
    color: $white;
    font-style: normal;
    font-weight: 400;
    line-height: 62px; /* 62px */
    text-transform: uppercase;
    max-width: 557px;
    flex: 1 1 auto;

    @include adaptive_fontSize(20px, 42)
  }
  a{
    color: #FFF;
    font-family: Barlow Condensed;
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%; /* 28px */
    text-transform: uppercase;
  }
}

.form_title {

  h3 {
    color: #FFF;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: 100%; /* 32px */
  }
}

.input_block {
  margin-top: 14px;
  span {
    color: #FFF;
    font-size: $fontSize22;
    font-style: normal;
    font-weight: 400;
    line-height: 154.3%; /* 33.946px */
  }

  input {
    padding: 21px 31px;
    width: 100%;
    border-radius: 4px;
    &::placeholder{
      color: #191923;
      font-size: 22px;
      font-style: normal;
      font-weight: 400;
      line-height: 154.3%; /* 33.946px */
    }

  }
  select{

  }
  span{
    color: #FFF;
    font-family: Commissioner;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 154.3%; /* 33.946px */
  }

}
.select_block{
  label{
    display: flex;
    gap: 14px;
  select{
     width: 30%;
    border-radius: 4px;

  }
  }
}
.remember_me{
   display: flex;
  gap: 10px;

  margin-top: 10px;
  color: #FFF;
  font-family: Commissioner;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 154.3%;
}
.form_contact_button{
   margin: 22px 0 0 ;
   padding: 14px 160px;
  border-radius: 4px;

}